.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-pack: center;
      justify-content: center;
}


.textual-clock-bg {
  padding: 50px;
  background: #111;
  color: #252525;
}

.textual-clock-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.textual-clock-label, .textual-clock-time-footer {
  display: inline-block;
  padding: 0.3em 0.3em;
  font-size: 3.5em;
  font-weight: 100;
  letter-spacing: 0.1em;
  word-spacing: 0.5em;
  text-shadow: 0px 0px 5px rgba(255,255,255, 0.15);

  vertical-align: middle;
}

.textual-clock-label.active {
  color: #fffad3;
  text-shadow: 0px 0px 10px rgba(255,255,255, 0.9);
  vertical-align: middle;
}

.textual-clock-time-footer {
  display: block;
  font-size: 1.5em;
  color: #9c27b0;

  margin-top: 50px;
  -ms-flex-item-align: end;
      align-self: flex-end;
  justify-self: flex-end;
}

@media only screen and (max-width: 900px) {
  .textual-clock-label, .textual-clock-time-footer, .textual-clock-label.active {
    font-size: 1.2em;
  }

  .textual-clock-bg {
    padding: 20px;
  }
}




